<template>
    <div class="container mt-5">

        <form>   
            <label for="input" class="form-label"><strong>¿Qué estás buscando?</strong></label>
            <div class="d-flex" >
                <input id="input" class="form-control w-50" type="text" placeholder="Buscar..." 
                list="ListaDatos" style="height: 38px" v-model="buscar" >

            <!--    <datalist id="ListaDatos">
                    <option :value="resulFinal"></option>
                </datalist>   -->

                <button class="btn btn-outline-dark" @click.prevent="busqueda()">Buscar</button>
            </div>
        </form>

        <div class="mt-3">
            <table class="table border border-1">
                <thead>
                    <tr class="text-primary">
                        <th class="display-6">Resultados:</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td class="estilo" @click="estado = !estado">{{resulFinal}}</td>
                    </tr>
                    <tr>
                        <td v-if="estado == true" class="lead p-5">{{resulFinalHistoria}}</td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
    
</template>

<script>

export default {

    name:'Busqueda',
    data(){
        return{
            buscar :'',
            resul : [],
            resulFinal: '',
            resulFinalHistoria:'',
            estado:false,
        }
    },
    methods:{
        busqueda(){
            this.resulFinal = ''
            let resulHistoriasAlex = JSON.parse(localStorage.getItem('Historias_Guardadas_Alex'));
            let resulHistoriasJuan = JSON.parse(localStorage.getItem('Historias_Guardadas_Juan'));
            //this.resul.push(resulHistoriasAlex,resulHistoriasJuan);    //incorrecto
            this.resul = resulHistoriasAlex.concat(resulHistoriasJuan);  //correcto
            console.log('array resul>>>',this.resul);
            this.buscar = this.buscar.toLowerCase(); //buscador a minuscula
            for(let item of this.resul){
                console.log('buscar>>>',this.buscar)
                if(item.titulo.toLowerCase().indexOf(this.buscar) !== -1){
                    console.log('resultado>>>',item.titulo)  
                    console.log('si hay resultado>>>',item.titulo.toLowerCase().indexOf(this.buscar))
                    //compara si existe la busqueda en un elemento del array
                    this.resulFinal = item.titulo
                    this.resulFinalHistoria = item.historia
                }else{
                    console.log('No hay Resultados similares',item.titulo.toLowerCase().indexOf(this.buscar))
                }
            }
            if(this.resulFinal === ''){ //si no encuentra resultados en el array...
                this.resulFinal = 'no hay resultados...'
            }     
        }
    }
}
</script>

<style scoped>
    .estilo{
        font-size: 30px ;
    }
    .estilo:hover{
        color: rgb(255, 255, 255);
        border: solid red;
        transition: color .9s ease;
        transition: border .9s ease;
        transition: background-color .3s ease;
        background-color: rgb(214, 119, 75);
    }
</style>>

