<template>
    <div class="container">
        <table class="table border border-1">
            <thead>
                <tr class="color bg-dark">
                    <th class="display-6">Las historias de Juan</th>
                </tr>
            </thead>
            <tbody class="lead">
                <tr class="bgcolor">
                    <td>
                        <div class="m-3">
                            <input type="text" class="form-control w-25" placeholder="Título" v-model="juan.articulo.titulo">
                        </div>
                        <div class="form-floating m-3">
                            <textarea class="form-control bg-light" id="floatingTextarea2" placeholder="efecto placeholder"
                            v-model="juan.articulo.historia" @keyup.enter="guardar_juan()" style="height: 100px"></textarea>
                            <label for="floatingTextarea2">Escribir Historia</label>
                        </div>
                        <div class="text-end m-3">
                            <button class="btn btn-outline-dark" @click="guardar_juan(juan.articulo.historia)">Guardar Artículo</button>
                        </div>
                    </td>
                </tr>                

                <tr v-for="item of juan.resul_juan" :key="item">
                    <td>
                        <strong class="text-danger p-3"><u>{{item.titulo}}</u></strong>
                        <div class="text-break fs-6 lead p-3 lh-sm bg-light">{{item.historia}}</div>
                    </td>          
                </tr>

            </tbody>
        </table>
    </div>
</template>

<script>

export default {
    name: 'Juan',
    data(){
        return {
            juan:{
                articulo:{
                    titulo:'',
                    historia: ""               
                },
                resul_juan: []               
            }
        }
    },
    methods:{
        guardar_juan(){
            this.juan.resul_juan.push(this.juan.articulo);
            this.juan.articulo = '',
            localStorage.setItem('Historias_Guardadas_Juan', JSON.stringify(this.juan.resul_juan));
            location.reload();
        }
    },
    created: function(){
        let historiasGuardadas = JSON.parse(localStorage.getItem('Historias_Guardadas_Juan'));
        //JSON.parse para obtener los datos en forma de array
        if(historiasGuardadas === null){
            this.juan.resul_juan = [];
        }
        else{
            this.juan.resul_juan = historiasGuardadas;
        }
    }
    /*
    mounted(){
        this.$emit('HistoriaJuan',this.resul_juan)
    }*/
}
</script>

<style>
    .color{
        color:greenyellow;
    }
    .bgcolor{
        background-color:rgb(209, 228, 182);
    }
</style>