<template>

    <div class="container">
        <table class="table border border-1">
            <thead>
                <tr class="bg-dark">
                    <th class="display-6 text-primary">Las historias de Alex</th>
                </tr>
            </thead>
            <tbody class="lead">
                <tr class="bgcolor">
                    <td>
                        <div class="m-3">
                            <input type="text" class="form-control w-25" placeholder="Título"
                            v-model="alex.articulo.titulo">
                        </div>
                        <div class="form-floating m-3">
                            <textarea class="form-control bg-light" id="floatingTextarea2" placeholder="efecto placeholder"
                            v-model="alex.articulo.historia" @keyup.enter="guardar_alex()" style="height: 100px"></textarea>
                            <label for="floatingTextarea2">Escribir Historia</label>
                        </div>
                        <div class="text-end m-3">
                            <button class="btn btn-outline-dark " @click="guardar_alex()">Guardar Articulo</button>
                        </div>
                    </td>
                </tr>
                <tr v-for="item of alex.resul_alex" :key="item">
                    <td>
                        <strong class="text-danger p-3"><u>{{item.titulo}}</u></strong>
                        <div class="text-break fs-6 lead p-3 lh-sm bg-light">{{item.historia}}</div>
                    </td>        
                </tr>
            </tbody>
        </table>
    </div>
</template>

<script>

export default {
    name: 'Alex',
    data(){
        return {
            alex:{
                articulo:{
                    titulo:'',
                    historia: ""
                },
                resul_alex: []             
            },
        }
    },
    methods:{
        guardar_alex(){
            this.alex.resul_alex.push(this.alex.articulo);
            this.alex.articulo = '' /////////////////////////////////////////////
            localStorage.setItem('Historias_Guardadas_Alex', JSON.stringify(this.alex.resul_alex));
            location.reload();
        },
    },
    created: function(){
        let historiasGuardadas = JSON.parse(localStorage.getItem('Historias_Guardadas_Alex'));
        //JSON.parse para obtener los datos en forma de array
        if(historiasGuardadas === null){
            this.alex.resul_alex = [];
        }
        else{
            this.alex.resul_alex = historiasGuardadas;
        }
    }
    /*
    mounted(){
        this.$emit('HistoriaAlex',this.resul_alex)
    }*/
}
</script>

<style scoped>
    .color{
        color:greenyellow;
    }
    .bgcolor{
        background-color:rgb(209, 228, 182);
    }
</style>